












































import axios from "axios";

export default {
  data() {
    return {
      public_maps: [],
      private_maps: [],
      public_maps_total: 0,
      private_maps_total: 0,
    };
  },
  created() {
    var ids = [];
    Object.keys(localStorage).forEach(function (key) {
      if (key.includes("map_")) {
        ids.push(key.replace("map_", ""));
      }
    });
    axios.get("/api/maps?orderBy=markers_count").then((response) => {
      this.public_maps_total = response.data.total;
      this.public_maps = response.data.data;
    });

    if (ids.length > 0) {
      axios
        .get("/api/maps", {
          params: {
            ids: ids,
            orderBy: "updated_at",
          },
        })
        .then((response) => {
          this.private_maps_total = response.data.total;
          this.private_maps = response.data.data;
        });
    }
  },
  computed: {},

  watch: {},

  methods: {},
};
